.card{background-color:var(--white);border-radius:var(--border-radius);-webkit-box-shadow:0px 0px 20px 0px rgba(0,0,0,.1);-moz-box-shadow:0px 0px 20px 0px rgba(0,0,0,.1);box-shadow:0px 0px 20px 0px rgba(0,0,0,.1);flex:1 0 350px;min-height:407px;padding:15px 18px 30px 22px;position:relative}.card__description{margin-top:auto}.card__info{position:absolute;right:20px;width:25px;transition:all .5s ease}.card__info:hover{transform:scale(1.05)}.card__content{height:100%;display:flex;flex-direction:column;justify-content:space-between;gap:54px}.card__img-wrapper{height:100%;display:flex;align-items:center;justify-content:center}.card__top-row{margin-bottom:25px}.card__row{display:flex;justify-content:space-between;align-items:center}.card__price{color:var(--accent-bright)}.card__price--discount{position:relative}.card__price--discount::after{position:absolute;content:"3527â‚½";display:block;color:var(--discount);font-size:13px;left:50%;transform:translateX(-50%);text-decoration:line-through}.card__rate{color:var(--accent-light);display:flex;gap:10px;align-items:center}.card__buy{color:var(--black);font-weight:var(--font-weight);border-bottom:2px solid rgba(0,0,0,0);transition:all 1s ease;position:relative}.card__buy::after{position:absolute;bottom:-2px;left:0;content:"";display:block;height:2px;width:20%;background-color:rgba(0,0,0,0);transition:all 1s ease}.card__buy:hover{transform:translateY(-2px)}.card__buy:hover::after{background-color:var(--accent-bright);width:100%}